<template>
	<view class="month-card" :class="{ 'month-card__selected': !!selected }">
		<view class="month-card_head">
			<view class="month-card_month" :style="{ color }">
				<view class="month-card_mId">
					<!-- 月份 -->
					{{ monthItem.m_id }}
				</view>
				<view>{{ monthItem.month }}</view>
			</view>
		</view>
		<view class="month-card_body">
			<view class="month-card_progress">
				<text class="month-card_progress_line" >
          <i :style="{ width: progress, backgroundImage: progressColor }"></i>
				</text>
				<text class="month-card_progress_num">{{ progress }}</text>
			</view>
			<view class="month-card_menu">
				<i class="fa fa-ellipsis-h"></i>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: "MonthCard",
		props: {
			monthItem: {
				type: Object,
				required: true
			},
			selected: {
				type: Boolean
			}
		},
		computed: {
			color() {
				return this.monthItem.colors[0]
			},
			progress () {
				const totalCount = this.monthItem.monthDays
				const doneCount = 2
				return `${Math.round((doneCount / totalCount) * 100)}%`
			},
			progressColor () {
				const colorLeft = `color-stop(30%, ${this.monthItem.colors[0]})`
				const colorRight = `to(${this.monthItem.colors[1]})`
				return `-webkit-gradient(linear, left bottom, right bottom, ${colorLeft}, ${colorRight})`
			},
		}
	}
</script>

<style lang="scss">
	// @import url('https://fonts.font.im/css?family=Nunito');
	
	.month-card {
		display: flex;
		flex-direction: column;
		height: 100%;
		flex: 1;
		margin: 0 16rpx;
		overflow: hidden;
		box-shadow: 0 20rpx 20rpx rgba(0, 0, 0, 0.3);
		border-radius: 20rpx;
		background-color: white;
		
		
		.month-card_head {
			display: flex;
			justify-content: space-between;
			margin-top: 5%;
			padding: 0 10%;
			color: black;
			transform: translate3d(0, 0, 0);
			will-change: transform;

			
			.month-card_month {
				font-size: 35rpx;
				font-family: 'Nunito', sans-serif;
				display: flex;
				flex-direction: column;
				text-align: center;
				
				.month-card_mId {
					font-size: 80rpx;
				}
			}
			
			i {
				color: #9c9c9c
			}
		}

		.month-card_body {
			display: flex;
			padding: 0 10%;
			transform: translate3d(0, 440rpx, 0);
			will-change: transform;

				
			.month-card_progress {
				flex: 1;
				display: flex;
				align-items: center;
				color: gray;
				
				.month-card_progress_line {
					flex: 0 0 50%;
					height: 6rpx;
					margin-right: 4%;
					background-color: #d3d7d4;
					
					i {
						display: block;
						height: 100%;
						transition: all 0.3s ease;
					}
				}
			}
			
			.month-card_menu {
					color: black
				}
		}
	}
</style>
